.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

td, tr {
  border: 1px solid black;
  height: 15px;
  width: 15px;
}
.table{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 30px;
}
.row{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

section{
  display: flex;
  flex-direction: row;  
}



.r-holder{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
